#root {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #d3d3d3;
}

.container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  background-color: #d3d3d3;
  width: 40%;
  height: 100%;
}
.bar {
  margin: 0 auto;
  width: 2px;
  height: 100px;
  background-color: blueviolet;
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
  padding: 10px;
  background-color: gray;
}
.btn {
  cursor: pointer;
  padding: 5px 10px;
  margin: 0 5px;
  outline: none;
  border: 1px solid black;
  border-radius: 5px;
}
